
export default {
  data ()  {
   return {
    title: "",
    apresentation: "",
    description: "",
    price: null,
    city: "",
    state: ""
   }
  },
  watch: {
    title(newValue) {
      this.$emit("update:title", newValue)
    },
    apresentation(newValue) {
      this.$emit("update:apresentation", newValue)
    },
    description(newValue) {
      this.$emit("update:description", newValue)
    },
    price(newValue) {
      this.$emit("update:price", newValue)
    },
    city(newValue) {
      this.$emit("update:city", newValue)
    },
    state(newValue) {
      this.$emit("update:state", newValue)
    },
  }
}