export default {
  data () {
    return {
      property: "",
      rooms: "",
      bathrooms: "",
      pets: ""
    }
  },

  watch: {
    property(newValue) {
      this.$emit("update:property", newValue)
    },
    rooms(newValue) {
      this.$emit("update:rooms", newValue)
    },
    bathrooms(newValue) {
      this.$emit("update:bathrooms", newValue)
    },
    pets(newValue) {
      this.$emit("update:pets", newValue)
    }
  }
}